import { Injectable } from '@angular/core';

import { AngularFireDatabase, AngularFireList } from 'angularfire2/database'
import { Usuario } from '../models/usuario';

@Injectable({
  providedIn: 'root'
})
export class UsuarioService {

  usuariosList: AngularFireList<any>;
  selectedUsuario: Usuario = new Usuario();

  constructor(private db: AngularFireDatabase) { }

  getUsuarios() {
    return this.usuariosList = this.db.list('usuarios');
  }

  insertUsuario(usuario: Usuario) {
    try {    
      const userRef = this.db.object(`usuarios/${usuario.uid}`);      
      userRef.set(usuario);
    } catch (error) {
      console.error(error);
    }
  }

  updateUsuario(usuario: Usuario) {
    this.usuariosList.update(usuario.uid, {
      uid: usuario.uid,
      displayName: usuario.displayName,
      photoUrl: usuario.photoURL,
      email: usuario.email,
      emailVerified: usuario.emailVerified,
      phoneNumber: usuario.phoneNumber
    });
  }

  deleteUsuario(uid: string) {
    this.usuariosList.remove(uid);
  }

}
