import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { AuthGuard } from '../../guards/auth.guard';
import { UsuarioService } from '../../services/usuario.service';
import { Usuario } from '../../models/usuario';

@Component({
  selector: 'login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  user = this.authService.user;
  errors = [];
  messages = [];
  showMessages = [];

  constructor(private authService: AuthService, private router: Router, private toastr: ToastrService, private authGuard: AuthGuard, private usuarioService: UsuarioService) { }

  ngOnInit() {
    this.authService.isAuthenticated().subscribe(
      result => {
        if (result == true) {
          this.router.navigate(['auth/dashboard']);
        }
      }
    );
    this.usuarioService.getUsuarios();
  }

  login(loginForm: NgForm) {    
    this.authService.authenticate(loginForm.value.email, loginForm.value.password).subscribe(
      success => {
        this.usuarioService.selectedUsuario = new Usuario();
        this.usuarioService.selectedUsuario.uid = success.user.uid;
        this.usuarioService.selectedUsuario.email = success.user.email;
        this.usuarioService.selectedUsuario.displayName = success.user.displayName;
        this.usuarioService.selectedUsuario.emailVerified = success.user.emailVerified;
        this.usuarioService.selectedUsuario.phoneNumber = success.user.phoneNumber;
        this.usuarioService.selectedUsuario.photoURL = success.user.photoURL;        
        this.usuarioService.insertUsuario(this.usuarioService.selectedUsuario);
        this.toastr.success('Usurio logado', 'Sucesso');
        this.router.navigate(['pages/dashboard']);
      },
      error => {
        this.toastr.error(error, 'Ops');
      }
    )
  }

  loginGoogle() {
    this.authService.authenticateGoogle();
  }

}
